	import uniSwipeAction from "@/components/uni-swipe-action/uni-swipe-action.vue";
	import uniLoadMore from "@/components/uni-load-more/uni-load-more.vue";

	export default {
		
		components: {
			uniSwipeAction,
			uniLoadMore
		},
		data() {
			return {
				options: [{
					text: '删除',
					style: {
						backgroundColor: "rgba(255,84,31,1)"
					}
				}],
				goodsList: [],
				//控制滑动效果
				theIndex: null,
				oldIndex: null,
				isStop: false,
				loading:"more",
				current_page:1 ,
				last_page:"",
				status: "more",
			}
		},
		methods: {
            //收藏
			requestColl(page){
				var _this = this;
				this.request({
				
					url: this.config.url + "member/collect",
					method: "post",
					data: {
						token: this.token,
				
					},
					success:(res) =>{
						console.log(res)
						if (res.data.code == 1) {
							_this.goodsList = res.data.data.data;
							this.last_page=res.data.data.last_page;
							this.status = "more";
							if (this.current_page >= this.last_page) {
								this.status = "noMore";
							}
						}
				
					}
				})
				
			},
			bindClick(options, id) {
				var _this = this;
				this.request({
					url: this.config.url + "goods/collect",
					method: "POST",
					data: {
						goods_id: id,
						token: this.token
					},
					success: function(res) {
						console.log(res)
						if (res.data.code == 1) {
							_this.goodsList.splice(options, 1)
						}
					}
				})
				
				// this.goodsList=this.goodsList.splice(options,2)
				// console.log(this.goodsList)
			},
			//控制左滑删除效果-begin
			touchStart(index, event) {
				//多点触控不触发
				if (event.touches.length > 1) {
					this.isStop = true;
					return;
				}
				this.oldIndex = this.theIndex;
				this.theIndex = null;
				//初始坐标
				this.initXY = [event.touches[0].pageX, event.touches[0].pageY];
			},
			touchMove(index, event) {
				//多点触控不触发
				if (event.touches.length > 1) {
					this.isStop = true;
					return;
				}
				let moveX = event.touches[0].pageX - this.initXY[0];
				let moveY = event.touches[0].pageY - this.initXY[1];

				if (this.isStop || Math.abs(moveX) < 5) {
					return;
				}
				if (Math.abs(moveY) > Math.abs(moveX)) {
					// 竖向滑动-不触发左滑效果
					this.isStop = true;
					return;
				}

				if (moveX < 0) {
					this.theIndex = index;
					this.isStop = true;
				} else if (moveX > 0) {
					if (this.theIndex != null && this.oldIndex == this.theIndex) {
						this.oldIndex = index;
						this.theIndex = null;
						this.isStop = true;
						setTimeout(() => {
							this.oldIndex = null;
						}, 150)
					}
				}
			},
			touchEnd(index, $event) {
				//结束禁止触发效果
				this.isStop = false;
			},

			//删除收藏

			deleteGoods(index, goodsId) {
				this.goodsList.splice(index, 1)
				// let len = this.goodsList.length;
				// for (let i = 0; i < len; i++) {
				// 	if (id == this.goodsList[i].id) {
				// 		this.goodsList.splice(i, 1);
				// 		break;
				// 	}
				// }
				// this.goodsList.splice(this.goodsList.indexOf(id), 1);
				// this.sum();
				// this.oldIndex = null;
				// this.theIndex = null;
				// 删除购物车后台；
				this.delCart(goodsId);
			},
			toGoods(id){
				uni.navigateTo({
					url: "/pages/goods/goods?id=" + id
				})
			},
			// 删除购物车
			delCart(id) {
				console.log("id", id)
				this.request({
					url: this.config.url + "goods/collect",
					method: "POST",
					data: {
						token: this.token,
						goods_id: id,
					},

					success: function(res) {
						console.log("res", res);
					},
				})

			},
		},
		onReachBottom() {
				this.current_page++
			if (this.current_page > this.last_page) {
				return;
			} else {
				this.requestColl(this.current_page);
			}
		},
		onLoad() {
			this.requestColl(this.current_page)
		}
	}
